.videos-collection{
  background-image: url(../img/videos-collection-bg.webp);
  background-size: cover;
  background-position: center top;
}

.video-slider{
  margin-top: 4.25em;
  .swiper-slide{
    width: 25%;
  }
}

.video-card{
  display: block;
  &__img{
    @include imgCover(177.4%);
    &:after{
      content: '';
      @include centerAbsolute;
      @include bgImgSize(100%);
      background-image: url(../img/play.svg);
      width: 8.5625em;
      height: 8.5625em;
    }
  }
}

.advantages{
  background: #2F3629;
  color: #FEFDF6;
  &__video{
    margin-top: 2.81em;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 6.3em),calc(100% - 2.4em) 100%,0 100%,0 0);
    video{
      width: 100%;
      display: block;
    }
  }
}

.advantage-item{
  &__top{
    display: flex;
    margin-bottom: .25em;
  }
  &__icon{
    width: 2.875em;
    flex-shrink: 0;
    margin-right: 1.375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 4.375em;
    font-weight: 600;
    text-transform: uppercase;
    margin-top: -.1875em;
  }
  &__measure{
    font-size: 1.5em;
    font-weight: 700;
    margin-left: .375em;
  }
}

.challenges{
  margin-top: 6.25em;
}

.challenge-card{
  &__img{
    padding-bottom: 67%;
  }
}


