@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 2.875em;
    &--cut{
        clip-path: polygon(0 0, 100% 0, 100% calc(100% - 1.5em), calc(100% - .5em) 100%, 0 100%, 0 0);
    }
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        letter-spacing: -.01em;
    }
}

.m-btn-black-outline{
    border: .125em solid $black;
    color: $black;
    &:hover{
        background-color: $black;
        color: $white;
    }
}

.m-btn-brown{
    background-color: $primary;
    color: $white2;
    &:hover{
        background-color: darken($primary,10%);
        color: $white2;
    }
}

.m-btn-black{
    background-color: $black;
    color: $white2;
    &:hover{
        background-color: darken($primary,10%);
        color: $white2;
    }
}
