.m-section{
  padding: 6.25em 0;
}

.services{
  background: #1B1D19;
  color: $white;
}

.service-card{
  padding: 2em 1.5em;
  background: #2F3629;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - 3.5em), calc(100% - 1.375em) 100%, 0 100%, 0 0);
  &__icon{
    height: 2.875em;
    flex-shrink: 0;
    margin-bottom: 1.25em;
  }
  &__title{
    font-size: 2.375em;
    line-height: 105%;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: -.02em;
  }
  &__desc,
  &__link span{
    font-size: 1.125em;
    line-height: 144%;
    letter-spacing: -.01em;
  }
  &__desc{
    color: #FDFFD1;
    margin-top: 1em;
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2.5em;
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: #FEFDF6;
    &:hover{
      color: #FEFDF6;
      opacity: .8;
    }
    img{
      width: .875em;
      margin-left: .625em;
    }
  }
}

.info-block{
  text-align: center;
  &__desc{
    width: 69em;
    max-width: 100%;
    margin: 1.75em auto 0;
    min-height: 4.8125em;
    .text18{
      line-height: 144%;
      letter-spacing: -.01em;
    }
  }
}

.technology{
  background: #2F3629;
  //color: #FDFFD1;
  color: #FEFDF6;
}

.technology-slider{
  margin-top: 3.75em;
  .swiper-slide{
    width: calc(100% / 3);
  }
  .slider__nav{
    margin-top: 3em;
  }
}

.challenge-card,
.technology-card{
  &__img{
    @include imgCover(77.4%);
    overflow: hidden;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 5em),calc(100% - 2em) 100%,0 100%,0 0);
  }
  &__content{
    margin-top: 1.25em;
    @include grid100gap(.75em);
  }
}


